import React from 'react';
import './App.css';

class Total extends React.Component {
    constructor(props) {
      super(props);
    }
    render() {
      let mystyle = {
        borderTop: "1px solid #ddd",
        backgroundColor: "#F6F6F6",
        fontSize: '14px'
      };
      return (
        <div className = "container card shadow-sm" style={{width: '300px'}}>
          <h3 className="row" style={{ fontWeight: 400, fontSize: '14px'}}>
            <span className="col-6">Items ({this.props.count})</span>
            <span className="col-6 text-right float-right">: ${this.props.actual.toFixed(2)}</span>
          </h3>
          <h3 className="row" style={{ fontWeight: 400, fontSize: '14px' }}>
            <span className="col-6">Discount</span>
            <span className="col-6 text-right float-right">: -${this.props.discount.toFixed(2)}</span>
          </h3>
          <h3 className="row" style={{fontWeight: 400, fontSize: '14px'}}>
            <span className="col-6">Type discount</span>
            <span className="col-6 text-right float-right">: -${this.props.typediscount.toFixed(2)}</span>
          </h3>
          <h3 className="row card-body" style={mystyle}>
            <span className="col-6">Order Total</span>
            <span className="col-6 text-right float-right">: ${this.props.total.toFixed(2)}</span>  
          </h3>
        </div>
      );
    }
  }
  
  export default Total;